.page-title {
	padding: 3em 0;
	background: linear-gradient(33.13deg, #27A9F8 9.78%, #0A8CBB 83.29%);
	color: #ffffff;
}
.body-text {
	padding-top: 3em;
	padding-bottom: 3em;
	text-align: justify;
}
.body-text p {
	padding: 0 3em;
}
@media screen and (max-width: 768px) {
	.body-text p {
		padding: 0;
	}
}
.external-content {
	background: #fafafa;
	color: #333;
	min-height: 97vh;
}
.footer-section-light {
  background: #ffffff;
  color: #333333;
  border-top: 2px solid #00b0f0;
  padding: 1em 0;
}
.footer-section-light .footer-link {
  color: #333333;
}
.footer-section-light .footer-link:hover,
.footer-section-light .footer-link:focus {
  color: #00b0f0 !important;
  text-decoration: none;
}
